@media screen and (min-height: 600px) and (min-width: 780px) {
  .warning {
    visibility: hidden;
  }
}

.warning {
  visibility: hidden;
  position: fixed;
  z-index: 1;
  height: 100vh;
  width: 100vw;
  background-color: black;
  color: red;
  font-weight: bold;
  font-size: 3rem;
}

.App {
  text-align: center;
  position: relative;
  height: 100vh;
  width: 100vw;
}

.side-bar {
  font-size: 0.98rem;
  position: fixed;
  width: 120px;
  height: 100vh;
  padding: 5px;
  background-color: rgb(203, 220, 228);
}

.side-bar > div {
  margin: 10px 0;
}

.side-bar > div:last-child {
  margin-top: 100px;
}

.side-bar > div:last-child > div:first-child {
  margin-top: 15px;
}

.game-container {
  --cont-w: calc(100vw - 140px);

  position: fixed;
  left: 130px;
  width: var(--cont-w);
  height: 100vh;
  background-color: darkgreen;
  display: grid;
  padding: 5px;

  grid-column-gap: 0;
  grid-row-gap: 10px;
  grid-template-columns: 1fr 1fr 1.5fr 1.5fr 1fr 1fr;
  grid-template-rows: repeat(3, 30%);
  grid-template-areas:
    ". . tm tm . ."
    "op1 op1 game game op2 op2"
    ". self self self self ."
}

.teammate,
.opponent-0,
.opponent-1,
.self {
  min-width: 100%;
}

.opponent-1 {
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
}

.opponent-0 {
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

.teammate {
  grid-area: tm;
  position: relative;
}

.opponent-0 {
  grid-area: op1;
  position: relative;
}

.opponent-1 {
  grid-area: op2;
  position: relative;
}

.game-info-container {
  grid-area: game;
  position: relative;
}

.other-hand-container {
  position: absolute;
  top: 0%;
  left: 48%;
  transform: translateX(-48%);
  white-space: nowrap;
}

.other-hand-container .outer-wrapper-small {
  transform: rotateX(90);
}

.other-player-name {
  position: absolute;
  top: 35%;
  width: 100%;
  text-align: center;
  font-weight: bold;
}

.other-role {
  position: absolute;
  top: 45%;
  width: 100%;
  text-align: center;
}

.self {
  margin-left: -10px;
  margin-right: -10px;
  grid-area: self;
  position: relative;
}

@media screen and (max-width: 1100px) {
  .self {
    margin-left: -80px;
    margin-right: -80px;
  }
}

.hand-container {
  position: absolute;
  height: 96%;
  width: 70%;
  top: 6%;
  text-align: center;
}

.challenge-desc {
  position: absolute;
  right: 2%;
  width: 110px;
  top: 2%;
  font-weight: bold;
}

.button-1,
.button-2,
.button-3 {
  font-size: 1.2rem;
  height: 40px;
  width: 100px;
}

.button-1 {
  position: absolute;
  top: 15%;
  right: 3%;
}

.button-2 {
  position: absolute;
  top: 40%;
  right: 3%;
}

.button-3 {
  position: absolute;
  top: 65%;
  right: 3%;
}

.console {
  position: absolute;
  width: calc(100% - 150px);
  height: 36px;
  bottom: 10px;
  left: 140px;
  background: rgba(204, 234, 238, 0.5);
  overflow: auto;
}

.console::-webkit-scrollbar {
  display: none;
}

.console > *:last-child {
  color: darkred;
}

.current-cards {
  position: relative;
  width: 90%;
  height: 75%;
  left: 5%;
  margin: 0;
}

.teammate-card {
  position: absolute;
  top: 0;
  width: 100%;
  text-align: center;
  transform: translateY(-80%);
}

.op-1-card {
  position: absolute;
  left: 0;
  height: 100%;
  top: 0;
  transform: translateX(-100px);
}

.op-2-card {
  position: absolute;
  height: 100%;
  right: 0;
  transform: translateX(80px);
}

.self-card {
  position: absolute;
  text-align: center;
  bottom: 0;
  transform: translateY(20px);
  width: 100%;
}

.discarded-cards {
  position: absolute;
  width: 20%;
  height: 30%;
  right: -30px;
  bottom: 10px;
  border-radius: 10px;
  text-align: left;
  z-index: -1;
  padding: 3px 0 0 3px;
}

.taken-cards {
  position: absolute;
  width: 20%;
  height: 30%;
  left: -30px;
  bottom: 10px;
  border-radius: 10px;
  text-align: left;
  z-index: -1;
  padding: 3px 0 0 3px;
}

.description {
  font-weight: bold;
  position: absolute;
  font-size: 0.8rem;
  width: 100%;
  bottom: -25px;
  text-align: center;
  white-space: nowrap;
  color: white;
  z-index: 1;

  transform: translateX(-17%);
}

.warning > * {
  margin-top: 150px;
  padding: 10px;
}

.popup {
  animation: 1s ease-out 0s 1 slideIn;
}

@keyframes slideIn {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

.home-container {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-color: aliceblue;
}

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.form-container input {
  margin: 10px;
  height: 25px;
  width: 150px;
  background-color: inherit;

  outline: none;
  border: none;
  border-bottom: 1px solid black;
}

.form-container select {
  margin: 10px;
  height: 25px;
  width: 150px;
  background-color: inherit;

  border: 1px solid black;
  border-radius: 4px;
}

.form-container button {
  margin: 10px;
  height: 25px;
  width: 100px;
}

.select-lang {
  position: fixed;
  top: 10px;
  right: 20px;
  width: 100px;
  height: 25px;
}

.alert {
  margin: 2px;
  font-size: 0.9rem;
  color: red;
}

.room-name {
  font-size: 2rem;
  font-weight: bold;
  margin: 20px;
}

.room-info {
  margin: 15px;
}

.room-players {
  position: fixed;
  width: 84%;
  height: 70%;
  top: 20%;
  left: 8%;

  display: flex;
  flex-direction: column;
}

.room-player {
  border: 1px solid green;
  border-radius: 5px;
  height: 15%;
  position: relative;

  font-size: 1.2rem;
}

.room-player .name {
  margin: auto;
  position: absolute;
  width: 40%;
  height: 100%;
  top: 0;
  left: 0;
  border-right: 1px solid black;
  background-color: rgb(250, 227, 169);
}

.room-player .role {
  position: absolute;
  width: 40%;
  height: 100%;
  top: 0;
  left: 40%;
  border-left: 1px solid black;
  background-color: rgb(250, 227, 169);
}

.room-player .ready,
.room-player .not-ready {
  position: absolute;
  width: 20%;
  height: 100%;
  top: 0;
  right: 0;
  border-left: 1px solid black;
}

.room-player .ready {
  background-color: lime;
}

.room-player .not-ready {
  background-color: red;
}

.inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 50%;
  margin: auto;
}

.room-player label {
  position: absolute;
  bottom: 5px;
  left: 5px;
  font-size: 1rem;
  color: rgba(57, 55, 2, 0.8);
}

.room-player {
  margin: 1% 0;
}

.toggle-ready {
  position: absolute;
  width: 100%;
  bottom: 10%;
}

.toggle-ready .ready,
.toggle-ready .not-ready {
  height: 42px;
  width: 140px;
  font-size: 1.2rem;
  border: none;
  border-radius: 10px;
}

.toggle-ready .ready {
  background-color: lime;
}

.toggle-ready .not-ready {
  background-color: red;
}

.selected-cards > div:nth-child(2) {
  margin-right: 30px;
}

.leave-game {
  transform: translateX(5px);
  position: absolute;
  bottom: 10px;
}

.leave-game button {
  font-size: 1.2rem;
  font-weight: bold;
  color: yellow;
  background-color: red;
  border-radius: 10px;
  height: 50px;
  width: 120px;
}

.draw-pile {
  width: 5rem;
  margin: auto;
  position: relative;
  transform: translateY(40px);
}

.result {
  position: fixed;
  top: 5vh;
  left: 140px;
  width: calc(100vw - 150px);
  height: 90vh;
  background-color: white;
  border-radius: 10px;
  background-color: coral;

  display: flex;
  flex-direction: column;
}

.result .title {
  margin: 20px;
  font-weight: bold;
  font-size: 2rem;
}

.result .summary {
  margin: 5px;
  font-size: 1.3rem;
}

.result .cards-summary {
  margin: 5px;
  font-size: 1.3rem;
}

.result .taken-cards-container {
  display: inline-flex;
}

.result .card {
  margin: 5px 20px;
}

.card-played-by {
  transform: translateX(15px);
  margin-top: 10px;
  font-size: 0.9rem;
}

.hidden-cards-reveal {
  position: absolute;
  transform: translateX(-80px) translateY(-30%);
  white-space: nowrap;
}

.hidden-cards-reveal > div {
  margin-right: 30%;
}

.title {
  color: white;
  font-weight: bold;
  font-size: 2rem;
  position: absolute;
  top: -20%;
  z-index: 1;
}

.master-number select {
  height: 30px;
  width: 100px;
  font-size: 1rem;
}

