body {
    --w: 4rem;
    --h: calc(var(--w) * 1.45);
}

.outer-wrapper-small {
    --w: 2rem;
    --h: calc(var(--w) * 1.45);
    width: calc(var(--w) - 22px);
    height: calc(var(--h) - 10px);
}

.outer-wrapper-medium {
    --w: 3.5rem;
    --h: calc(var(--w) * 1.45);
    width: calc(var(--w) - 30px);
    height: calc(var(--h) - 11px);
}

.outer-wrapper {
    width: calc(var(--w) - 40px);
    height: calc(var(--h) - 13px);
}

@media only screen and (min-width: 900px) {
    body {
        --w: 8rem;
    }

    .outer-wrapper-small {
        --w: 3rem;
        --h: calc(var(--w) * 1.45);
        width: calc(var(--w) - 30px);
        height: calc(var(--h) - 10px);
    }

    .outer-wrapper-medium {
        --w: 5rem;
        --h: calc(var(--w) * 1.45);
        width: calc(var(--w) - 50px);
        height: calc(var(--h) - 11px);
    }

    .outer-wrapper {
        width: calc(var(--w) - 100px);
        height: calc(var(--h) - 13px);
    }
}


.outer-wrapper,
.outer-wrapper-small,
.outer-wrapper-medium {
    display: inline-block;
    position: relative;
}

.card-wrapper,
.card-wrapper-active,
.card-wrapper-active-small,
.card-wrapper-small,
.card-wrapper-medium {
    position: absolute;
    left: 0;
    display: inline-block;
    overflow: hidden;
    width: var(--w);
    height: var(--h);
}

.card-wrapper > img,
.card-wrapper-active > img,
.card-wrapper-active-small > img,
.card-wrapper-small > img,
.card-wrapper-medium > img {
    height: auto;
    position: relative;
    object-fit: cover;
    max-width: calc(var(--w) * 13);
}

.card-wrapper-active:hover,
.card-wrapper-active-small:hover {
    transform: translateY(-10px)
}

